	module.exports = {"pathContext":{"group":[{"node":{"id":"be08b17e-6863-5d49-bb13-0bcbba379aca","slug":"five-years-old-and-growing","title":"Five years old and growing","excerpt":"<p>Five years ago today, BMJ Open appeared on the scene. Conceived as a general medical journal to provide authors a fast, transparent route to publication, BMJ Open could have developed in many different ways. Happily it has developed into a journal we’re proud of, and despite its broad scope, it has grown into a journal [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/02/23/five-years-old-and-growing/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 23, 2016","template":"","format":"standard"}},{"node":{"id":"5d598440-ddf4-50e3-9538-629d2231ca1d","slug":"membership-of-socialcommunity-groups-after-retirement-linked-to-longer-life","title":"Membership of social/community groups after retirement linked to longer life","excerpt":"<p>Membership of social groups, such as book clubs or church groups, after retirement is linked to a longer life, with the impact on health and wellbeing similar to that of regular exercise, suggests research published in the online journal BMJ Open. The more groups an individual belongs to in the first few years after s/he stops [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/02/16/membership-of-socialcommunity-groups-after-retirement-linked-to-longer-life/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 16, 2016","template":"","format":"standard"}},{"node":{"id":"edb21e8c-4bb5-5790-a64c-091e9c914f1a","slug":"higher-nurse-to-patient-ratio-linked-to-reduced-risk-of-inpatient-death","title":"Higher nurse to patient ratio linked to reduced risk of inpatient death","excerpt":"<p>Death rates 20 per cent lower in hospitals with 6 or fewer patients per registered nurse A higher nurse to patient ratio is linked to a reduced risk of inpatient death, finds a study of staffing levels in NHS hospitals, published in the online journal BMJ Open. In trusts where registered (professionally trained) nurses had [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/02/09/higher-nurse-to-patient-ratio-linked-to-reduced-risk-of-inpatient-death/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 09, 2016","template":"","format":"standard"}},{"node":{"id":"d0fe4cd4-4abc-521d-b3c4-618f5a834127","slug":"the-nhs-health-check-in-england-bereavement-by-suicide-and-public-involvement-in-medical-research-most-read-articles-in-january","title":"The NHS Health Check in England, bereavement by suicide, and public involvement in medical research: Most read articles in January","excerpt":"<p>NHS Health Checks, the risks of bereavement by suicide, and public involvement in medical research The Top 10 Most Read list for January is almost completely comprised of new entries, with the exception of the ever popular review by Kristensen et al on the effects of statins. In the top spot this month is an evaluation [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/02/05/the-nhs-health-check-in-england-bereavement-by-suicide-and-public-involvement-in-medical-research-most-read-articles-in-january/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 05, 2016","template":"","format":"standard"}},{"node":{"id":"ad2a057e-7ebd-5db4-aa3e-e10ae87bd961","slug":"requesting-clinical-trial-protocols","title":"Requesting clinical trial protocols","excerpt":"<p>We have recently made some changes to our submission system. If we receive a clinical trial, we will now ask that authors include the original trial protocol. Editor-in-chief, Dr Trish Groves, explains why: Transparent reporting of research has always been at the heart of BMJ Open. From the very start the journal has published all [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/19/requesting-clinical-trial-protocols/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 19, 2016","template":"","format":"standard"}},{"node":{"id":"af35f47b-ca9d-583b-b33d-5a70a3243917","slug":"editors-picks-2015-part-seven","title":"Editors’ Picks 2015: Part Seven","excerpt":"<p>Polypharmacy and potentially inappropriate prescription of medicines This week we have been looking at some of our staff editors’ favourite articles from 2015. These papers have covered a variety of important issues in medical research, from the transparency of clinical trials to the publication of negative findings. Our final editors’ pick takes us to Ireland, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/10/editors-picks-2015-part-seven/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 10, 2016","template":"","format":"standard"}},{"node":{"id":"55764dbd-a637-5ce2-b118-f1ba6d78b43d","slug":"editors-picks-2015-part-six","title":"Editors’ Picks 2015: Part Six","excerpt":"<p>The 2D:4D digit ratio and autism risk: is there an association? In December we surveyed our staff editors to tell us about their favourite articles from 2015. Our penultimate editors&#8217; pick investigates the relationship between autism risk and a putative marker of testosterone exposure: the 2D: 4D digit ratio. In spite of large investments in [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/09/editors-picks-2015-part-six/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 09, 2016","template":"","format":"standard"}},{"node":{"id":"be080966-c8aa-5c09-96a0-ae5ddafc1781","slug":"editors-picks-2015-part-five","title":"Editors’ Picks 2015: Part Five","excerpt":"<p>How well reported are interventions in systematic reviews? In December we surveyed our staff editors to tell us about their favourite articles from 2015. Part 5 focuses on another important editorial issue: the quality of reporting in systematic reviews. Clinicians and healthcare professionals often rely on systematic reviews and meta-analyses for information on how effective interventions [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/08/editors-picks-2015-part-five/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 08, 2016","template":"","format":"standard"}},{"node":{"id":"1b39b744-c37c-5e10-bf0a-8b111e33d7dd","slug":"editors-picks-2015-part-four","title":"Editors’ Picks 2015: Part Four","excerpt":"<p>Long-term health effects of exercise for the elderly: study protocol Each day this week we&#8217;ve been looking at a published paper from 2015 that sparked the interest of one of our staff editors. Our fourth pick is a study protocol for a randomised controlled trial of exercise training in an elderly population. BMJ Open supports [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/07/editors-picks-2015-part-four/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 07, 2016","template":"","format":"standard"}},{"node":{"id":"880ec165-d53c-542b-965f-62d7fbdb1aba","slug":"antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december","title":"Antidepressants and risk of mania, medically unexplained symptoms and the association between alcohol and mortality in Alzheimer&#8217;s: Most read articles in December","excerpt":"<p>Antidepressants, medically unexplained symptoms, and alcohol consumption in Alzheimer&#8217;s patients December saw a lot of new papers entering the Top 10 Most Read, with half of them being from the current issue. Patel et al&#8216;s research paper, complete with video abstract and press release, questions whether antidepressants increase the risk of mania and bipolar disorder [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/06/antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 06, 2016","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":9,"pageCount":26,"additionalContext":{}}}

